<template>
<button type="button" @click="play" class="button-play btn btn-primary btn-sm mx-1">BrE<img :src="`${publicPath}listen-icon.svg`" alt="">
</button>
</template>

<script>
export default {
  name: 'button-bre-play',
  data() {
    return {
      publicPath: process.env.BASE_URL
    }
  },  
  props: {
    href: String
  },
  methods: {
    play() {
      var loader = document.getElementById('loader');
      loader.style.visibility = "visible";
      var audio = new Audio(this.publicPath+this.href);
    audio.addEventListener("canplaythrough", () => {
      loader.style.visibility = "hidden";
      audio.play();
    });
    },
  },   
}
</script>

