<template>
	<div>
	<div class="d-flex mt-3 word-exercise">
		<div class="letters-container p-2 flex-fill">

			<vue-word-cloud
			:words="this.list"
			:rotation="([, weight]) => weight == 9 ? 90 : weight == 7 ? 45 : -45"
			rotation-unit="deg"
			:spacing="spacing"
			font-family="Arimo"
			>

			<template slot-scope="{text, weight}">
				<div :title="text" @click="onWordClick(text)" :id="`id${weight}`" class="letter noselect">
					{{ text }}
				</div>
			</template>
			</vue-word-cloud>	
		</div>
		<div class="p-2 flex-fill wordInBoxContainer">
		<div class="wordInBox border flex-fill my-2">{{wordInBox}}</div>
		</div>
	</div>

	<div class="d-flex justify-content-end align-items-center my-2">

	<transition name="bounce">
	<div :class="{shake: isShake, shake2: isShake2}" v-if="showinc" class="me-2"><span class="my-0 text-danger">Incorrect!</span></div>
	
	<div v-if="showcor" class="me-2"><span class="my-0 me-2 text-success">Correct!</span>
	<span class="badge bg-primary">EN:</span>
    <span class="mx-1">{{wordEn}}</span>
	<span class="badge bg-secondary">RU:</span>
    <span class="ms-1">{{wordRu}}</span>	
	</div>
	</transition>

	<div><button @click="clear" type="button" class="btn btn-primary btn-sm ms-1">
	<img :src="`${publicPath}clear.svg`" alt="">
	Clear</button></div>

	<div><button type="button" class="btn btn-primary btn-sm ms-1" 
	@click="check">
	<img :src="`${publicPath}check.svg`" alt="">
	Check</button></div>

	</div>
	</div>	
</template>

<script>
	import VueWordCloud from 'vuewordcloud'
	export default {
		name: 'word',
		components: {
			VueWordCloud    
		},
		data() {
			return {
				publicPath: process.env.BASE_URL,
				showinc: false,
				showcor: false,
				isShake: false,
				isShake2: true,
				spacing: 1,
				wordInBox: '',      
				list: [],
				wordEn: process.env.VUE_APP_WORD_EN,
				wordRu: process.env.VUE_APP_WORD_RU,     
			}
		},  
		props: {
		},
		methods: {
			onWordClick(letter) {
				this.wordInBox += letter;
			},
			clear() {
				this.wordInBox = '';
				this.showcor = false;
				this.showinc =false;
			},

			shuffle(array) {
				var j, x, i;
				for (i = array.length - 1; i > 0; i--) {
					j = Math.floor(Math.random() * (i + 1));
					x = array[i];
					array[i] = array[j];
					array[j] = x;
				}
				return array;
			},  	

			check() {
				this.isShake = !this.isShake;
				this.isShake2 = !this.isShake2;
				if(this.wordInBox == this.wordEn) {
					this.showcor = true;
					this.showinc = false;
				}
				else {
					this.showinc = true;
					this.showcor = false;
				}
			}
		}, 
		mounted() {
			let array = this.shuffle(this.wordEn.split(""));
			//let array = [...new Set(array)];
			let weights = [9, 7 ,5];
			array.map((letter) => this.list.push([letter,weights[Math.floor(Math.random()*weights.length)]])); 
		}

	}
</script>

