<template>
  <div>
      <draggable 
      class="list-inline border p-2 flex-fill my-2" 
      :list="list" 
      v-bind="dragOptions" 
      @start="drag = true" 
      @end="drag = false" 
      group="sentence" 
      @change="log" 
      style="min-height: 60px;">
        <transition-group type="transition" :name="!drag ? 'flip-list' : null">
        <div
          style="cursor: move;"
          class="list-inline-item my-1 button-exercise noselect"
          v-for="(word, index) in list"
          :key="`key-${index+1}`">
          {{ word }}
        </div>
        </transition-group>
      </draggable>

  <div class="d-flex justify-content-end align-items-center">
    <div :class="{shake: isShake, shake2: isShake2}" v-if="showinc"><span class="my-0 me-2 text-danger">Incorrect!</span></div>
    <transition name="bounce">
      <div v-if="showcor" class="me-2"><span class="my-0 me-2 text-success">Correct!</span>
      <button-bre-play :href="hrefbre"></button-bre-play>
      <button-ame-play :href="hrefame"></button-ame-play> 
      </div>
    </transition>  
    <div><button type="button" class="btn btn-primary btn-sm ms-1" 
    @click="check">
    <img :src="`${publicPath}check.svg`" alt="">
    Check</button></div>
  </div> 
  </div>
</template>

<script>
import draggable from 'vuedraggable' 
import ButtonAmePlay from './ButtonAmePlay.vue'
import ButtonBrePlay from './ButtonBrePlay.vue' 
export default {
  name: 'exercise',
  components: {
    draggable,
    ButtonAmePlay,
    ButtonBrePlay,    
  },
  data() {
    return {
      publicPath: process.env.BASE_URL,
      showinc: false,
      showcor: false,
      isShake: false,
      isShake2: true,
      list: [],
      drag: false      
    }
  },  
  props: {
    sentence: String,
    hrefbre: String,
    hrefame: String
  },
  methods: {
    log: function(evt) {
      window.console.log(evt);
    },

    shuffle(array) {
      var j, x, i;
      for (i = array.length - 1; i > 0; i--) {
          j = Math.floor(Math.random() * (i + 1));
          x = array[i];
          array[i] = array[j];
          array[j] = x;
      }
      return array;
    },

    check() {
      this.isShake = !this.isShake;
      this.isShake2 = !this.isShake2;
      if(this.list.join(" ") == this.sentence) {
        this.showcor = true;
        this.showinc = false;
      }
      else {
        this.showinc = true;
        this.showcor = false;
      }
    }
  }, 
  mounted() {
    this.list = this.shuffle(this.sentence.split(" "));
  },

  computed: {
    dragOptions() {
      return {
        animation: 200,
        group: "sentence",
        disabled: false,
        ghostClass: "ghost"
      };
    }
  }    
}
</script>

