<script>
import ButtonAmePlay from './components/ButtonAmePlay.vue'
import ButtonBrePlay from './components/ButtonBrePlay.vue'
import TextExercise from './components/TextExercise.vue'
import AudioExercise from './components/AudioExercise.vue'
import Word from './components/Word.vue'
import cards from './assets/cards.json'
import TextExercisesJson from './assets/text.exercises.json'
import AudioExercisesJson from './assets/audio.exercises.json'

export default {
  name: 'App',
  components: {
    ButtonAmePlay,
    ButtonBrePlay,
    TextExercise,
    AudioExercise,
    Word
  },

  data() {
    return {
      cards: cards,
      TextExercisesJson: TextExercisesJson,
      AudioExercisesJson: AudioExercisesJson,
      wordEn: process.env.VUE_APP_WORD_EN,
      wordRu: process.env.VUE_APP_WORD_RU,
      subtitle: process.env.VUE_APP_SUBTITLE,
      level: process.env.VUE_APP_LEVEL,
      transcriptionBr: process.env.VUE_APP_TRANSCRIPTION_BR,
      transcriptionAm: process.env.VUE_APP_TRANSCRIPTION_AM,
      publicPath: process.env.BASE_URL
    }
  },

  created(){
    let loader = document.getElementById('loader');
    loader.style.visibility = "hidden"; 
  }, 

  computed: { 
    cardsWithWordHighlighted: function() {
      var re = new RegExp(`(${this.wordEn})`, 'gi');
      var stringifiedCards = JSON.stringify(this.cards);
      var stringifiedCardsWithHighlightedWord = stringifiedCards.replaceAll(re, '<b>$1</b>');
      return JSON.parse(stringifiedCardsWithHighlightedWord);
    }
  }    
}
</script>

